const player ={
    i:1,
    newString:undefined,
    time:100,
    id:undefined,
    table:{
        '.start':'start',
        '.stop':'pause',
        '.slow':'slow',
        '.normal':'normal',
        '.fast':'fast'
    },
    bindEvents:()=>{
        for (let key in player.table) {
            if(player.table.hasOwnProperty(key)){ //è¿™é‡Œæ£€æŸ¥å¦‚æœkeyæ˜¯playerçš„tableé‡Œé¢çš„è‡ªèº«çš„å±æ€§ï¼Œè€Œä¸æ˜¯å…¶ä»–çš„å±æ€§ï¼ˆåæ¥æ·»åŠ çš„ï¼‰
                const value = player.table[key]
                document.querySelector(key).onclick =player[value]
            }
        }
    },
    init:()=>{
        player.bindEvents()
    },
    run:()=>{
        if(player.i>writeString.length){
            window.clearInterval(player.id)
            return
        }
        player.newString = writeString.substr(0,player.i)
        player.i+=1
        document.querySelector("#styleTag").innerHTML= player.newString
        document.querySelector("#code").innerText=player.newString
        document.querySelector("#code").scrollTop=9999
    },
    play:(time)=>{
        player.id = setInterval(player.run,time)
    },
    pause:()=>{
        window.clearInterval(player.id)
    },
    slow:()=>{
        player.pause()
        player.play(500)
    },
    start:()=>{
        player.pause()
        player.play(300)
    },
    normal:()=>{
        player.pause()
        player.play(100)
    },
    fast:()=>{
        player.pause()
        player.play(0)
    }
}

player.init()

